@use "./styles/theme" as theme;
@use "./styles/sizes" as sizes;

.maron {
  background-color: theme.$maron;
}

.swamp {
  background-color: theme.$swamp;
}

a,
a:hover,
a:link,
a:visited,
a:active {
  text-decoration: none;
  color: white;
}

::selection {
  background: theme.$port;
}

::-moz-selection {
  background: theme.$port;
}